.Button_button__1eCoJ{
    position: relative;
    margin: 10px;
    overflow: hidden;
    cursor: pointer;
}

.Button_button__1eCoJ::after{
    display: block;
    content: '';
    position: absolute;

    left: -130%;
    top: 0;
    height: 100%;
    width: 20%;

    transform: skew(45deg);

    background-color: #d8ecd3;
    opacity: 0.7;

    z-index: 14;
}

.Button_button__1eCoJ:hover::after{
    animation: Button_blink__1t9Ok 0.35s ease;/*//https://html5book.ru/css3-animation/*/
}

.Button_button__1eCoJ:hover::before{
    display: block;
    content: '';
    position: absolute;

    left: -100vw;
    top: 0;
    height: 100vh;
    width: 300vw;

    background-color: #d8ecd3;
    opacity: 0.2;

    z-index: 7;
}

.Button_button__1eCoJ:focus{
